// import index.scss
@import '../../index.scss';

.tour {
    background-color: $mainGrey;
    margin: 2rem 0;
    img {
        width: 100%;
        display: block;
        // min-height: 13rem;
    }
}

@media screen and (min-width: 768px){
    .tour {
        display: grid;
        grid-template-columns: 2fr 3fr;
        grid-column-gap: 1rem;
    }
}

.imgContainer {
    position: relative;
}

.closeBtn {
    position: absolute;
    display: block;
    top: 5%;
    right: 5%;
    color: $mainWhite;
    font-size: 1.4rem;
    cursor: pointer;
    transition: all 0.3s linear;
    // Or we can setup $mainTransition in index.scss file
    // transition: $mainTransition;
    opacity: 0;
}

.imgContainer:hover .closeBtn {
    opacity: 1;
}

.tourInfo {
    padding: 1rem 0.5rem;
    h3 {
        text-transform: uppercase;
        color: $mainGreen;
    }
    h4 {
        text-transform: capitalize;
        color: $mainBlack;
        margin: 0.2rem 0 0.6rem 0;
    }
    h5 {
        display: flex;
        align-items: center;
        text-transform: capitalize;
        span {
            color: $mainGreen;
            margin: 0.6rem;
            font-size: 1.2rem;
            cursor: pointer;
        }
    }
}